:root {
  /* Colors */
  --color-primary: #7c3aed; /* Accent purple */
  --color-text-dark: #111827;
  --color-text-muted: #6b7280;
  --color-background: #f6f6f7;
  --color-card-bg: #ffffff;
  --color-tag-popular: var(--color-primary);
  --color-tag-sold: #ef4444; /* Red for sold out */
  --color-star-filled: #fde047; /* Yellow/Gold for rating */
  --color-star-empty: #d1d5db;

  /* Layout and Styling */
  --shadow-sm: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -2px rgba(0, 0, 0, 0.06);
  --shadow-md: 0 10px 15px -3px rgba(0, 0, 0, 0.1), 0 4px 6px -4px rgba(0, 0, 0, 0.1);
  --radius-card: 12px;
}

/* Global Reset */
* { box-sizing: border-box; }
body {
  font-family: 'Inter', system-ui, -apple-system, 'Segoe UI', Roboto, 'Helvetica Neue', Arial, sans-serif;
  margin: 0;
  background: var(--color-background);
  color: var(--color-text-dark);
  min-height: 100vh;
}

/* Header & Controls */
.header { 
  padding: 2rem 1.5rem 0; 
  text-align: center;
}
.header h1 { 
  margin: 0;
  font-size: 2rem;
  font-weight: 800;
}
.subtitle { 
  color: var(--color-text-muted); 
  margin-top: 0.5rem;
  font-size: 1rem;
}

.coffee-list-section { 
  padding: 1.5rem; 
  max-width: 1200px;
  margin: 0 auto;
}
.controls {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.5rem;
  padding: 0 0.5rem;
  font-size: 0.95rem;
  color: var(--color-text-dark);
}
.toggle {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}
.count {
  font-weight: 500;
  color: var(--color-text-muted);
}

/* Grid Layout */
.coffee-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(260px, 1fr)); /* Larger minimum size */
  gap: 1.5rem; /* More spacing */
}

/* Card Component */
.card {
  background: var(--color-card-bg);
  border-radius: var(--radius-card);
  box-shadow: var(--shadow-md);
  transition: transform 0.2s, box-shadow 0.2s;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}
.card:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 20px rgba(0, 0, 0, 0.15);
}

.card-media {
  position: relative;
  height: 180px; /* Slightly taller */
  background: #e5e7eb;
}
.card-media img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: opacity 0.3s;
}

/* Tags */
.tag {
  position: absolute;
  left: 12px;
  top: 12px;
  padding: 4px 10px;
  border-radius: 999px;
  font-size: 0.75rem;
  font-weight: 700;
  color: white;
  z-index: 10;
}
.tag.popular { background: var(--color-tag-popular); }
.tag.sold { background: var(--color-tag-sold); }

/* Card Body */
.card-body {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  flex-grow: 1; /* Ensures buttons align at the bottom */
}
.coffee-name {
  margin: 0;
  font-size: 1.15rem;
  font-weight: 700;
}
.meta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 0.5rem;
}
.price {
  font-weight: 800;
  color: var(--color-primary);
  font-size: 1.15rem;
}

/* Rating */
.rating {
  display: flex;
  gap: 0.4rem;
  align-items: center;
  font-size: 0.9rem;
}
.stars {
  color: var(--color-star-filled);
  letter-spacing: 1px;
}
.rating-num {
  font-weight: 600;
}
.votes {
  color: var(--color-text-muted);
}

/* Actions/Button */
.card-actions {
  margin-top: 1rem;
}
.btn {
  width: 100%;
  padding: 0.65rem 0.75rem;
  border-radius: 8px;
  border: 0;
  background: var(--color-primary);
  color: white;
  font-weight: 600;
  cursor: pointer;
  transition: background 0.2s;
}
.btn:hover:not([disabled]) {
  background: #6d28d9;
}
.btn[disabled] {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Unavailable State */
.unavailable {
  opacity: 0.8;
  filter: grayscale(10%);
}
.unavailable .btn[disabled] {
  background: #9ca3af; /* A softer color for disabled button */
}

/* Status Messages */
.status { 
  padding: 1rem;
  text-align: center;
  color: var(--color-text-muted);
}
.status.error { color: var(--color-tag-sold); }

/* Responsive tweaks */
@media (max-width: 520px) {
  .card-media { height: 160px; }
  .controls { flex-direction: column; gap: 0.75rem; }
}